import axios from './axios';

/* ====================== 订单管理相关 ==================== */
export const createOrder = (data) => {
  return axios({
    method: 'POST',
    url: '/order/order/create',
    headers: {
      token: '',
    },
    data,
  });
};

export const updateOrder = (data) => {
  return axios({
    method: 'post',
    url: '/order/order/update',
    headers: {
      token: '',
    },
    data,
  });
};


export const deleteOrder = (data) => {
  return axios({
    method: 'post',
    url: '/order/order/delete',
    headers: {
      token: '',
    },
    data,
  });
};

export const submitOrder = (data) => {
  return axios({
    method: 'post',
    url: '/order/order/commit',
    headers: {
      token: '',
    },
    data,
  });
};

export const getOrderDetail = (orderNo) => {
  return axios({
    method: 'get',
    url: `/order/order/detail?orderNo=${orderNo}`,
    headers: {
      token: '',
    },
  });
};

export const getOrderProgress = (orderNo) => {
  return axios({
    method: 'get',
    url: `/order/order/progress/get?orderNo=${orderNo}`,
    headers: {
      token: '',
    },
  });
};

export const getOrderList = ({
  limit, cursor, status, type,
}) => {
  return axios({
    method: 'get',
    url: '/order/order/userList',
    params: {
      limit,
      cursor,
      status,
      type,
    },
    headers: {
      token: '',
    },
  });
};
