<template>
  <div class='order-table'>
    <el-table
      :data="orderList"
      :header-cell-style="{'background-color': '#F5F5F5', 'color': '#333', 'padding-left': '10px', 'padding-right': '10px'}"
      :cell-style="{'padding-left': '10px', 'padding-right': '10px'}">
      <template slot="empty">
        <div class="empty-block">
          <img :src="require('@/assets/img/order-management/empty_table.png')" alt="空白图片" width="171" height="137">
          <p>{{$t('order_management.table.no_order')}}</p>
          <div class="grid grid-3 empty-tip">
            <p>{{$t('order_management.table.no_order_desc_1')}}</p>
            <img :src="require('@/assets/img/order-management/btn.png')" alt="新建按钮" width="66" height="40" v-if="$i18n.i18next.language === 'cn'">
            <img :src="require('@/assets/img/order-management/btn-en.png')" alt="新建按钮" width="66" height="40" v-else>
            <p class="text-align-left">{{$t('order_management.table.no_order_desc_2')}}</p>
          </div>
        </div>
      </template>
      <el-table-column prop="orderNo" :label="$t('order_management.table.order_no')" min-width="196"> </el-table-column>
      <el-table-column prop="companyName" :label="$t('order_management.table.company_name')" min-width="148px" />
      <el-table-column prop="season" :label="$t('order_management.table.season')" min-width="196" />
      <el-table-column
        :label="$t('order_management.table.delivery_by')"
        min-width="196">
        <template slot-scope="scope">
          <span style="">{{displayableTime( scope.row.deliveryTime, true )}}</span>
        </template>
      </el-table-column>
      <el-table-column prop="handlerName" :label="$t('order_management.table.handler')" min-width="196"> </el-table-column>
      <!-- <el-table-column
        prop="gmtCreateTime"
        :label="$t('order_management.table.created_at')"
        min-width="196">
        <template slot-scope="scope">
          <span>{{displayableTime( scope.row.gmtCreateTime, false )}}</span>
        </template>
      </el-table-column> -->
      <el-table-column prop="brand" :label="$t('order_management.table.brand')" min-width="196" />
      <el-table-column prop="category" :label="$t('order_management.table.category')" min-width="196" />
      <el-table-column
        prop="orderType"
        :label="$t('order_management.table.type')"
        min-width="196px">
        <template slot-scope="scope">
          <span>{{showOrderType(scope.row.orderType)}}</span>
        </template>
      </el-table-column>
      <el-table-column
        prop="status"
        :label="$t('order_management.table.status')"
        min-width="196px">
        <template slot-scope="order">
          <orderStatusText :status="order.row.status" />
        </template>
      </el-table-column>
      <el-table-column
        :label="$t('order_management.table.progress')"
        min-width="120px"
        align="center">
        <template slot-scope="orders">
          <i class="iconfont iconjindu" v-if="ableToShowProcess(orders.row.status)"
            @click="showProgress(orders.row.orderNo)"></i>
          <i class="iconfont iconjindu disable" v-else></i>
        </template>
      </el-table-column>
      <el-table-column
        :label="$t('order_management.table.detail')"
        min-width="100px"
        align="center">
        <template slot-scope="orders">
          <i class="iconfont iconbianji1" v-if="ableToShowDetail(orders.row.status)"
            @click="editOrder(orders.row.orderNo)"></i>
          <i class="iconfont iconbianji1 disable" v-else></i>
        </template>
      </el-table-column>
      <el-table-column
        :label="$t('order_management.table.delete')"
        min-width="100px"
        align="center">
        <template slot-scope="orders">
          <i class="iconfont iconshanchu" v-if="ableToDelete(orders.row.status)"
            @click="deleteOrder(orders.row.orderNo)"></i>
          <i class="iconfont iconshanchu disable" v-else></i>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>
<script>
import { defineComponent } from '@vue/composition-api';
import i18n from 'i18next';
import formatDate from '@/filters/date';
import orderStatusText from './orderStatus.vue';

export default defineComponent({
  components: {
    orderStatusText,
  },
  props: {
    orderList: {
      type: Array,
      default: () => {
        return [];
      },
    },
  },
  setup(props, ctx) {
    function displayableTime(timestamp, useHour) {
      return formatDate(timestamp, `YYYY/MM/DD HH:${useHour ? '00' : 'mm'}`);
    }
    const showOrderType = (orderType) => {
      return i18n.t(`order_management.table.type_list.${orderType - 1}`);
    };
    const ableToShowProcess = (status) => {
      return status >= 5;
    };
    const ableToDelete = (status) => {
      return status === 1;
    };
    const ableToShowDetail = (status) => {
      return status >= 1 && status !== 0;
    };

    const editOrder = (orderNo) => {
      ctx.root.$router.push({ name: 'orderManagementForm', query: { orderNo } });
    };

    const showProgress = (orderNo) => {
      ctx.root.$router.push({ name: 'orderProgress', query: { orderNo } });
    };
    const deleteOrder = (orderNo) => {
      ctx.root.$confirm(i18n.t('order_management.delete_confirm.desc'), i18n.t('order_management.delete_confirm.title'), {
        confirmButtonText: i18n.t('order_management.delete_confirm.cancel'),
        cancelButtonText: i18n.t('order_management.delete_confirm.confirm'),
        showClose: false,
        distinguishCancelAndClose: true,
        callback: (action) => {
          if (action === 'cancel') {
            ctx.emit('delete-order', orderNo);
          }
        },
      });
    };

    return {
      displayableTime,
      showOrderType,
      ableToShowProcess,
      ableToDelete,
      ableToShowDetail,
      editOrder,
      showProgress,
      deleteOrder,
    };
  },
});
</script>
