














































import useLoader from '_/composables/use-loader';
import { getOrderList, deleteOrder } from '@/api/order-management';
import {
  defineComponent, ref, reactive, toRefs, watch, computed,
} from '@vue/composition-api';
import { envs } from '../../utils/envs';
import { orderStatusList, orderTypeList } from './order-table/orderTable';
import OrderTable from './order-table/index.vue';

type SelectionItem = {
  label: string,
  value: number
};
export default defineComponent({
  components: {
    OrderTable,
  },
  setup(props, { root }) {
    watch(() => root.$route.query.page, () => {
      if (!root.$route.query.page) {
        root.$router.push({ query: { page: '1' } });
      }
    }, {
      immediate: true,
    });
    const t = ref(Date.now());
    const page = computed(() => {
      return root.$route.query.page || '1';
    });
    const total = ref(0);
    const status = ref([]);
    const types = ref([]);
    const loading = ref(false);
    const statusList = orderStatusList as SelectionItem[];
    const typeList = orderTypeList as SelectionItem[];
    const filters = reactive({
      status: [],
      type: [],
    });
    const records = useLoader(async () => {
      loading.value = true;
      const res = (await getOrderList({
        limit: 10,
        cursor: page.value,
        status: filters.status.join(','),
        type: filters.type.join(','),
      })).data;
      total.value = res.total;
      loading.value = false;
      return res.records;
    }, reactive({
      page,
      ...toRefs(filters),
      t,
    }), {
      initialValue: [],
    });
    async function handleDeleteOrder(orderNo: string) {
      await deleteOrder({ orderNo });
      t.value = Date.now();
      // 删除成功后要更新订单列表
      // const index = records.value.indexOf(records.value.find(i => i.orderNo === orderNo));
      // records.value.splice(index, 1);
    }
    async function search() {
      filters.status = status.value;
      filters.type = types.value;
      root.$router.push({
        query: {
          ...root.$route.query,
          page: '1',
        },
      });
    }
    return {
      loading,
      filters,
      total,
      status,
      statusList,
      types,
      typeList,
      records,
      handleDeleteOrder,
      search,
      page,
    };
  },
});
